import React, {Component} from 'react'

class Weather extends Component {
  
  state = {
    temperature:0,
    weather:"",
  };

  componentDidMount() {
   const urlPath = "http://api.openweathermap.org/data/2.5/weather?q=warwick,uk"
   const APPID = "&APPID=6cff2d77d480a2df6ef946144d45dfe0"
   
   fetch(urlPath+APPID)
      .then(res => res.json())
      .then((rows)=>
      {
        this.setState({temperature: Math.round(rows.main.temp -273.15)})
        this.setState({weather:rows.weather[0].main})
      })
    
  }

  render() {
    return (
    <>{this.state.temperature}&deg;C {this.state.weather}</>
      );
  }
}

export default Weather 