import React, {Component} from 'react'
import Clock from './clock'
import Train from './trains'
import Weather from './weather'

const Group = (props) =>{
  const rows = props.items.map((item) =>{
    return(
      <li><a href={item.url}>{item.name}</a></li>
      )
  })
  
  return(
    <ul>{rows}</ul>
  )
}

const Groups = (props) =>{
  const rows = props.groupdata.map((iter) =>{
    return(
           <div className="group">
            <h1>{iter.title}</h1>
            <Group items={iter.items} />      
            </div>
      )
  })
 
  return(
           <>{rows}</>
    )
}

class App extends Component {
 //     {title:"Train",items:["17.07 - ok", "17.52 - ok", "18.12 - ok"]}
 
  state ={
    groupdata: [
      {title:"News",items:[{name:"BBC", url:"http://www.bbc.co.uk/news"}, {name:"FT", url:"http://www.ft.com"}, {name:"Guardian",url:"http://www.guardian.co.uk"}]},
      {title:"Pensions",items:[{name:"TPR", url:"https://www.thepensionsregulator.gov.uk/"}, {name:"TPR - exchange", url:"https://login.thepensionsregulator.gov.uk/"}, {name:"PPF",url:"https://www.ppf.co.uk/"},{name:"Legislation", url:"http://www.legislation.gov.uk/"}, {name:"Institute",url:"https://www.actuaries.org.uk"}]},
         ]
  }
  
 
  render(){

    let dt = new Date()
    let from = (dt.getHours()>12 ? "bsw": "wrp")
    let to = (dt.getHours()>12 ? "wrp": "bsw")
      return (
      <div>
        <head>
          <title>Home</title>
          <link rel="shortcut icon" type="image/png" href="assets/favicon.png" />
          <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Raleway:200,400" />
        </head>
        <body>
	        <div id="logo">
		        <Clock/>
          <h2>{dt.getDate()}-{dt.getMonth()+1}-{dt.getFullYear()}, <Weather /></h2>
          </div>
	        <div id="content">
          <Groups groupdata={this.state.groupdata}/>
          <Train from={from} to={to}/>
          </div>
      </body>
     </div>
)
}
}

export default App;
